import { TouristService } from './../../../Services/tourist.service';
import { Component, OnInit,ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Platform, IonSlides } from '@ionic/angular';
import { ModalController } from '@ionic/angular';
import { CityDetailsModalPage } from '../../../Modals/city-details-modal/city-details-modal.page';
import { RegisterService } from 'src/app/Services/register.service';
import { Images } from 'src/environments/environment.prod';
import { CityDetails } from 'src/app/Models/city-details';
@Component({
  selector: 'app-city-details',
  templateUrl: './city-details.page.html',
  styleUrls: ['./city-details.page.scss'],
})
export class CityDetailsPage implements OnInit {
  @ViewChild(IonSlides, {static: true}) slider: IonSlides;
  modalDataResponse: any;
  slideOpts = {
    effect: 'flip',
    autoplay: {
    delay: 4000
    }
  };
  id: any;
  cityData:CityDetails;
  photoPath:any = Images
  sliderPhotos:any[]=[]
  disc:any;
  constructor(public modalController: ModalController,
    private actRoute:ActivatedRoute,
    private touristService:TouristService,
    public registerService:RegisterService,
    ) {
      this.cityData = new CityDetails();
     }

  ngOnInit() {
  }
  ionViewWillEnter(){
    
    this.id = this.actRoute.snapshot.params.id;
    this.touristService.GetCityDetails(this.id).then(res=>{
      this.cityData = JSON.parse(res.data)
      this.registerService.title = this.cityData.name
      this.sliderPhotos = this.cityData.photos.slice(0,8)
      this.disc = this.cityData.description.replace(/<[^>]+>/g, '')
    })
    this.slider.startAutoplay();
  }
  async initModal() {
    const modal = await this.modalController.create({
      component: CityDetailsModalPage,
      cssClass: 'my-custom-modal-css',
      componentProps: {
        'id':  this.id
      }
    });

    modal.onDidDismiss().then((modalDataResponse) => {
      if (modalDataResponse !== null) {
        this.modalDataResponse = modalDataResponse.data;
        console.log('Modal Sent Data : '+ modalDataResponse.data);
      }
    });

    return await modal.present();
  }
  public ionViewWillLeave(){  
    this.slider.stopAutoplay();
   }
   slidesDidLoad(slides) {
    this.slider.startAutoplay();
  }
}
